import Vue from 'vue'
import Vuex from 'vuex'
import VuexPersist from "vuex-persist"
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    lwh_flag: true,
    filterId: "",
    lm_isClose:false,   //控制立即登录弹框状态
    lm_isHint:true,
    lm_mobile:"18568571773",
    lm_sex:"保密",
    lm_Date:"请选择",
    lm_Site:"请选择",
    lm_Subject:"请选择",
    lm_Class:"请选择"
  },
  mutations: {
    lwh_btn(s, v) {
      s.lwh_flag = v
    },
    filterId(state, id) {
      state.filterId = id
      console.log(state.filterId);
    },
    lm_Close(state,bool){
      state.lm_isClose = bool
    },
    lm_Hint(state,bool){
      console.log(bool);
      state.lm_isHint = bool
    },
    changeMobile(state,value){
      state.lm_mobile = value
    },
    changeSex(state,value){
      state.lm_sex = value
    },
    changeDate(state,value){
      state.lm_Date = value
    },
    changeSite(state,value){
      state.lm_Site = value
    },
    changeSubject(state,value){
      state.lm_Subject = value
    },
    changeClass(state,value){
      state.lm_Class = value
    }
  },
  actions: {
  },
  getters:{
    
  },
  modules: {
  },
  plugins: [
    new VuexPersist({
      storage: window.localStorage
    }).plugin
  ]
})
